import { DataStore } from './../../models/data-store';
import { VeiculoService } from './../../providers/api/veiculo.service';
import { ViewChild } from '@angular/core';
import { Content, ToastController } from 'ionic-angular';
import { Macro } from './../../models/macros';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

/**
 * Generated class for the MacroPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-macro',
  templateUrl: 'macro.html',
})
export class MacroPage {
  macros: Macro[];
  public veiculo: DataStore;

  @ViewChild(Content) content: Content;

  constructor(public navCtrl: NavController, public navParams: NavParams, public veiculoService:VeiculoService, public toastCtrl: ToastController ) {
    this.macros = this.navParams.get("macros");
    this.veiculo = this.navParams.get("veiculo");
    this.scrollToBottom();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad MacroPage');
  }

  scrollToBottom() {
    setTimeout(() => {
      if (this.content.scrollToBottom) {
        this.content.scrollToBottom();
      }
    }, 400);
  }


  doRefresh(refresher) {
    this.veiculoService
    .getMacrosVeiculo(this.veiculo.veiculoId)
    .subscribe(
      (res: any) => {
        this.macros = res;
      },
      error => {
        let toast = this.toastCtrl.create({
          message: 'Falha: Tente novamente!',
          duration: 3000,
          position: 'top'
        });
        toast.present();
        refresher.complete();
      },
      () => {
        refresher.complete();
      }
    );
}

}



