import { DatePipe } from '@angular/common';
import { TranslateService } from '@ngx-translate/core';
import { Pipe, PipeTransform, Inject, LOCALE_ID } from '@angular/core';

@Pipe({name: 'formatdate'})
export class FormatDatePipe extends DatePipe implements PipeTransform {
  
  constructor(@Inject(LOCALE_ID) private _locale: string,private translate: TranslateService) {
    super(_locale);
  }
  
  transform(value: Date): any {
    const browserLang = this.translate.getBrowserLang();
    
    if (browserLang) {
      if (browserLang === 'pt' || browserLang === 'pt-br') {
        return super.transform(value,'dd/MM/yyyy HH:mm:ss');
      }
    } 

    return super.transform(value,'MM/dd/yyyy h:mm:ss a');
  }
}


